<script>
import PatientService from '@services/patient'

export default {
  props: {
    visit: {
      type: Object,
      required: true,
    },
    invoice: {
      type: Object,
      required: true
      ,
    },
    items: {
      type: Array,
      default: function () {
        return []
      },
    },
    stores: {
      type: Array,
      default: function () {
        return []
      },
    },
  },

  data () {
    return {
      prescriptions: [],

      fields: [
        {
          name: 'prescribed_at',
          title: 'Date',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          callback: this.$options.filters.date_DATE_MONTH_YEAR,
          width: '10%',
        },
        {
          name: 'item.name',
          title: 'Drug',
          titleClass: 'ps-3',
          dataClass: 'ps-3 text-truncate',
          width: '30%',
        },
        {
          name: 'dosage',
          title: 'Dosage',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          width: '10%',
        },
        {
          name: 'frequency',
          title: 'Frequency',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          width: '15%',
          callback: function (value) {
            return value + ' times'
          },
        },
        {
          name: 'duration',
          title: 'Duration',
          titleClass: 'ps-3',
          dataClass: 'ps-3',
          width: '15%',
          callback: function (value) {
            return value + ' day(s)'
          },
        },
        {
          name: 'user.name',
          title: 'Written By',
          titleClass: 'ps-3',
          dataClass: 'ps-3 text-truncate',
          width: '10%',
        },
        {
          name: '__slot:actions',
          dataClass: 'text-center',
          width: '10%',
        },
      ],
      moreParams: {
        filter: { patient_id: this.visit.patient_id },
        include: 'items,users',
        sort: '-created_at',
      },

      prescription: {},

      form: new window.Form({
        id: null,
        patient_visit_id: this.visit.id,
        item_id: '',
        intake_id: '',
        comments: '',
        dosage: '',
        frequency: 1,
        duration: 1,
        quantity: 0,
        unit_price: 0,
      }),

      viewForm: false,

      // items: [],
      intakes: [],

      // patientVisitRecords: this.patientVisits,

      availableStock: 0,
      priceRecord: 0,
    }
  },

  computed: {
    // vuetable () {
    //   return this.$refs.table.$refs.vuetable
    // },

    prescriptionsTotal () {
      if (!this.prescriptions.length) {
        return 0
      }

      const vm = this

      return this.prescriptions.reduce((accumulator, current) => {
        let price = Number(0)

        if (vm.invoice.cash_credit === 0) {
          price = current.item.cash_price ? current.item.cash_price.charge_value * 0.01 : 0
        }

        if (vm.invoice.cash_credit === 1) {
          price = current.item.scheme_price ? current.item.scheme_price.charge_value * 0.01 : 0
        }

        return Number.parseFloat(price) + accumulator
      }, 0)
    },

  },

  watch: {
    'moreParams.filter.patient_visit_id': function (newId) {
      if (newId) {
        this.vuetable.refresh()
      }
    },

    'form.item_id': function (newId) {
      if (newId) {
        const store = this.stores.find(store => store.name === 'Pharmacy')

        if (store) {
          this.findAvailableStock(store.id)
        }
      }
    },
  },

  created () {
    this.findPrescriptions()
  },

  // mounted () {
  //   this.findIntakes()
  // },

  methods: {

    findPrescriptions () {
      this.$httpClient.get('/prescriptions', {
        params: {
          filter: { patient_id: this.visit.patient_id },
        },
      }).then(({ data }) => {
        this.prescriptions = data.data
      }).catch()
    },

    showPrescriptionDetail (prescription) {
      this.prescription = prescription

      this.$bvModal.show('prescription-detail-modal')
    },

    createPrescription () {
      this.form.post('/prescriptions')
        .then(() => {
          this.findPrescriptions()

          this.$toast.success('Prescription has been added')
        })
        .catch((error) => {
        })
    },

    updatePrescription () {
      this.form.patch('/prescriptions/' + this.form.id)
        .then(() => {
          this.findPrescriptions()

          // this.vuetable.reload()

          this.$bvModal.hide('prescription-update-modal')

          this.$toast.success('Prescription updated')
        })
        .catch()
    },

    deletePrescription (rowData) {
      this.$toast.question('Are you sure', 'Delete Prescription').then(() => {
        PatientService.deletePrescription(rowData.id)
          .then(() => {
          this.$toast.info('Prescription deleted')

              this.findPrescriptions()
              
          })
          .catch(() => {
            this.$toast.error('Something went wrong! Prescription not deleted')
          })
      })
    },

    findIntakes () {
      PatientService.findAllPrescriptionIntake()
        .then(({ data }) => {
          this.intakes = data
        })
        .catch()
    },
    findAvailableStock (storeId) {
      this.availableStock = 0

      // if (this.form.item_id) {
      this.$httpClient.get('/available_stock/' + this.form.item_id + '/' + storeId)
        .then(({ data }) => {
          this.availableStock = data
        }).catch(error => {
          console.log(error)
        })
      // }
    },
  },
}
</script>

<template>
  <div>
    <div class="card rounded-3 mt-3">
      <div class="card-header">
        <h2 class="page-title">
          Prescriptions
        </h2>
        <div class="card-actions">
          <button
            v-b-modal:prescription-modal
            :disabled="visit.posted_at"
            class="btn btn-outline-primary"
          >
            Prescribe
          </button>
        </div>
      </div>

      <table class="table table-bordered">
        <thead>
          <tr>
            <th class="ps-2">
              Date
            </th>
            <th class="ps-2">
              Drug
            </th>
            <th class="ps-2">
              Dosage
            </th>
            <th class="ps-2">
              Frequency
            </th>
            <th class="ps-2">
              Duration (days)
            </th>
            <th class="ps-2">
              Written By
            </th>
            <th class="ps-2">
              Price
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="drug in prescriptions">
            <td class="ps-3">
              {{ drug.prescribed_at | date_DATE_MONTH_YEAR }}
            </td>
            <td class="ps-3">
              {{ drug.item.name }}
            </td>
            <td class="ps-3">
              {{ drug.dosage }}
            </td>
            <td class="ps-3">
              {{ drug.frequency }}
            </td>
            <td class="ps-3">
              {{ drug.duration }}
            </td>
            <td class="ps-3">
              {{ drug.user.name }}
            </td>
            <td v-if="invoice.cash_credit === 0" class="ps-3">
              {{ (drug.item.cash_price ? drug.item.cash_price.charge_value * 0.01 : 0) | toMoneyFormat }}
            </td>
            <td v-if="invoice.cash_credit === 1" class="ps-3">
              {{ (drug.item.scheme_price ? drug.item.scheme_price.charge_value * 0.01 : 0) | toMoneyFormat }}
            </td>
            <td>
              <button class="btn btn-white py-1" @click="deletePrescription(drug)">
                <i class="uil uil-trash-alt  text-danger me-1" />
              </button>
            </td>
          </tr>
          <tr>
            <td />
            <td />
            <td />
            <td />
            <td />
            <td />
            <td class="ps-2">
              {{ prescriptionsTotal | toMoneyFormat }}
            </td>
          </tr>
        </tbody>
      </table>
      <!--      <VueTable-->
      <!--        ref="table"-->
      <!--        api-url="/prescriptions"-->
      <!--        :fields="fields"-->
      <!--        :append-params="moreParams"-->
      <!--      >-->
      <!--        <template #actions="props">-->
      <!--          <button class="btn btn-white py-1" @click="deletePrescription(props.rowData)">-->
      <!--            <i class="uil uil-trash-alt  text-danger me-1" />-->
      <!--          </button>-->
      <!--        </template>-->
      <!--      </VueTable>-->
    </div>

    <b-modal
      id="prescription-modal"
      title="Prescription Form"
      title-class="font-18"
      no-close-on-backdrop
      hide-header-close
      modal-class="modal-blur"
      content-class="bg-white rounded-3"
      size="lg"
      footer-class="d-flex justify-content-between bg-muted-lt border-top"
    >
      <div class="mb-4">
        <label for="" class="form-label">Medication
          <small v-if="form.item_id" class="form-label-description text-indigo">Available stock:
            {{
              availableStock
            }} units </small> </label>

        <v-select
          v-model="form.item_id"
          :options="items"
          label="name"
          :reduce="(option) => option.id"
          :clearable="false"
          name="item_id"
        >
          <template #selected-option="item">
            <div>
              <div class="font-weight-medium">
                {{ item.name }}
              </div>
              <div v-if="invoice.cash_credit === 0" class="text-primary small">
                KES {{ (item.cash_price ? item.cash_price.charge_value * 0.01 : 0) | toMoneyFormat }}
              </div>
              <div v-else class="text-primary small">
                KES {{
                  (item.scheme_price ? item.scheme_price.charge_value * 0.01 : 0) | toMoneyFormat
                }}
              </div>
            </div>
          </template>
        </v-select>

        <span
          v-if="form.hasError('item_id')"
          class="text-danger"
          v-text="form.getError('item_id')"
        />
      </div>

      <div class="row">
        <div class="col-lg-4">
          <div class="mb-4">
            <label for="" class="form-label">Dosage</label>

            <input v-model="form.dosage" type="text" class="form-control" placeholder="20mg">

            <span
              v-if="form.hasError('dosage')" class="text-danger"
              v-text="form.getError('dosage')"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="mb-4">
            <label for="" class="form-label">Frequency</label>
            <input v-model="form.frequency" type="number" class="form-control">

            <span
              v-if="form.hasError('frequency')" class="text-danger"
              v-text="form.getError('frequency')"
            />
          </div>
        </div>
        <div class="col-lg-4">
          <div class="form-group">
            <label for="" class="form-label">Duration <small class="text-indigo">(days)</small></label>
            <input v-model="form.duration" type="number" class="form-control" min="1">

            <span
              v-if="form.hasError('duration')"
              class="text-danger"
              v-text="form.getError('duration')"
            />
          </div>
        </div>
      </div>

      <!--      <div class="mb-3">-->
      <!--        <label for="" class="form-label">Remarks <small class="text-muted">(optional)</small></label>-->
      <!--        <textarea id="" v-model="form.comments" name="" cols="30" rows="2" class="form-control" />-->

      <!--        <span v-if="form.hasError('comments')" class="text-danger" v-text="form.getError(comments)" />-->
      <!--      </div>-->

      <template #modal-footer="{cancel}">
        <button
          class="btn btn-light me-2"
          :disabled="form.processing"
          @click="cancel"
        >
          Close
        </button>

        <button
          type="submit"
          class="btn btn-primary"
          :disabled="form.processing"
          @click="createPrescription"
        >
          Prescribe
        </button>
      </template>
    </b-modal>

    <b-modal
      id="prescription-update-modal"
      title="Prescription Form"
      title-class="font-18"
      no-close-on-backdrop
      scrollable
      footer-bg-variant="light"
      hide-header-close
      size="lg"
    >
      <form
        id="prescriptionUpdateForm"
        @submit.prevent="updatePrescription"
      >
        <div class="row">
          <div class="col-md-8">
            <div class="form-group">
              <label for="provisional_diagnosis">Medicine</label>
              <v-select
                v-model="form.item_id"
                :options="items"
                label="name"
                :reduce="(option) => option.id"
                :clearable="false"
                name="item_id"
              />

              <div
                v-if="form.errors.has('item_id')"
                class="text-danger"
                v-text="form.errors.first('item_id')"
              />

              <span v-if="form.item_id" class="small text-purple">Available stock: {{
                availableStock
              }}</span>
            </div>
          </div>

          <div class="col-md-4">
            <div class="form-group">
              <div class="">
                <label
                  for="dosage"
                  :class="{ 'border-danger': form.errors.has('dosage') }"
                >Dosage
                </label>
              </div>

              <b-form-input
                id="dosage"
                v-model="form.dosage"
                type="number"
                min="1"
              />

              <small v-if="form.errors.has('dosage')" class="text-danger">
                {{ form.errors.first('dosage') }}
              </small>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <div class="d-flex justify-content-between align-items-baseline">
                <label
                  for="dosage"
                  :class="{ 'border-danger': form.errors.has('frequency') }"
                >Frequency
                  <i
                    id="frequency_helpTex"
                    class="uil uil-question-circle text-muted small"
                  />
                </label>

                <b-tooltip target="frequency_helpTex" variant="secondary">
                  How frequent should the drug be taken?
                </b-tooltip>

                <small class="text-muted">
                  {{ form.frequency }} times</small>
              </div>

              <b-form-input
                id="dosage"
                v-model="form.frequency"
                type="number"
                min="1"
                max="20"
              />

              <small v-if="form.errors.has('frequency')" class="text-danger">
                {{ form.errors.first('frequency') }}
              </small>
            </div>
          </div>

          <div class="col-md-4">
            <div class="form-group">
              <div class="d-flex justify-content-between align-items-baseline">
                <label
                  for="duration"
                  :class="{ 'border-danger': form.errors.has('duration') }"
                >
                  Duration
                  <i
                    id="duration_helpTex"
                    class="uil uil-question-circle text-muted small"
                  />
                </label>

                <b-tooltip target="duration_helpTex" variant="secondary">
                  How long (in days) should the medicine be taken?
                </b-tooltip>

                <small class="text-muted">
                  {{ form.duration }} day(s)</small>
              </div>

              <b-form-input
                id="dosage"
                v-model="form.duration"
                type="number"
                min="1"
                max="20"
              />

              <small v-if="form.errors.has('duration')" class="text-danger">
                {{ form.errors.first('duration') }}
              </small>
            </div>
          </div>

          <div class="col-md-4">
            <div class="form-group">
              <label
                for="intake_id"
                :class="{ 'border-danger': form.errors.has('intake_id') }"
              >
                Intake
                <i
                  id="intake_id_helpTex"
                  class="uil uil-question-circle text-muted small"
                />

                <b-tooltip target="intake_id_helpTex" variant="secondary">
                  On what condition should the prescription be taken?
                </b-tooltip>
              </label>
              <v-select
                id="intake_id"
                v-model="form.intake_id"
                :options="intakes"
                label="name"
                :reduce="(option) => option.id"
                :clearable="false"
              />
              <small v-if="form.errors.has('intake_id')" class="text-danger">
                {{ form.errors.first('intake_id') }}
              </small>
            </div>
          </div>
        </div>

        <div class="form-group">
          <label for="comments">Remarks</label>
          <b-form-textarea
            id="comments"
            v-model="form.comments"
            class="shadow-sm"
          />
          <span v-if="form.errors.has('comments')" class="text-danger">
            {{ form.errors.first('comments') }}
          </span>
        </div>
      </form>

      <template #modal-footer="{cancel}">
        <div
          class="d-flex justify-content-between align-items-center bg-light-alpha"
        >
          <button
            class="btn btn-light mr-2"
            :disabled="form.processing"
            @click="cancel"
          >
            Close
          </button>

          <button
            form="prescriptionUpdateForm"
            type="submit"
            class="btn btn-success"
            :disabled="form.processing"
          >
            {{ form.processing ? 'Processing...' : 'Update' }}
          </button>
        </div>
      </template>
    </b-modal>

    <!--        <PrescriptionDetail :prescription="prescription"/>-->
  </div>
</template>
